<template>

  <div id="pay1">
    <h1 class="cloorg">月度仓储费</h1>
    <a href="#">请提供您对此页面的反馈</a>
    <p>这是您储存在亚曼运营中心的每个ASIN的预计月度仓储费 <a href="#">了解更多信息</a></p>

    <!-- <span class="dow">
      下载
    </span>
    <div class="btnBox">
      <Button type="warning">请求下载</Button>
    </div> -->

    <Tabs value="name1" class="mt10">
        <TabPane label="下载" name="name1" class="btnBox1">
             <Form :model="formLeft" label-position="left" :label-width="100">
                    <FormItem label="动作月份">
                        <Row>
                            <Col span="11">
                                <DatePicker type="date" placeholder="日期" v-model="formLeft.date"></DatePicker>
                            </Col>
                        </Row>
                    </FormItem>
                <Button type="warning">请求下载</Button>
            </Form>
        </TabPane>
        <TabPane label="在线查看" name="name2" class="btnBox">
           暂不支持在线查看
        </TabPane>
        
    </Tabs>
     <p>动作日期17-01-01 - 17-01-02</p>
      <Table border :columns="columns1" :data="data1"></Table>

      
    
  </div>
</template>

<script>
export default {
  name: "pay1",
  data() {
    return {
      columns1: [
        {
          title: "报告类型",
          key: "order1"
        },
        {
          title: "涵盖的日期范围",
          key: "order2"
        },
        {
          title: "请求日期",
          key: "order3"
        },
        {
          title: "报告状态",
          key: "order4"
        }
      ],
      data1: [
        {
          order1: "您尚未请求任何报告",
          order2: "",
          order3: "",
          order4: ""
        }
      ],
         formLeft: {
                    date: ''
                },

    };
  }
};
</script>

<style>
#pay1 {
  display: inline-block;
  width: 78%;
  position: absolute;
  top: 0px;
  left: 238px;
  padding: 20px 30px;
  min-width: 600px;
}
.cloorg {
  color: orange;
}
.dow {
  display: inline-block;
  width: 80px;
  height: 35px;
  border: 1px solid #cccccc;
  text-align: center;
  line-height: 35px;
  border-bottom: none;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  margin-top: 30px;
}
.btnBox1 {
  width: 100%;
  border: 1px solid #cccccc;
  padding: 20px;
  margin-bottom: 220px;
  
}

.pSpan {
  display: inline-block;
  height: 40px;
}
</style>



