  width: 80px;
  height: 35px;
  border: 1px solid #cccccc;
  text-align: center;
  line-height: 35px;
  border-bottom: none;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  margin-top: 30px;
}
.btnBox {
